import { useRef, useState } from "react";
import "./styles.css";

export default function App() {
  const [count, SetCount] = useState(0);

  const ref = useRef(null);
  const Starting = () => {
    if (!ref.current) {
      ref.current = setInterval(() => {
        SetCount((prev) => prev + 1);
      }, 1000);
    }
  };

  const Passsing = () => {
    clearInterval(ref.current);
    ref.current = null;
  };
  const restarting = () => {
    SetCount(0);
  };

  return (
    <div className="App">
      <h3>UseRef</h3>
      <h3>{count}</h3>
      <button onClick={Starting}>start</button>
      <button onClick={Passsing}>pause</button>
      <button onClick={restarting}>Reset</button>
    </div>
  );
}
